
.bet .settings-col {
  width: 550px;
  padding: 0 0 0 30px;
}
.bet .settings-col,
.bet .settings-col-20 {
  position: relative;
  height: 91%;
  float: left;
  overflow: auto;
}
.bet .settings-col-20 {
  width: 16%;
  min-width: 178px;
  border-right: 1px dashed #777;
  padding: 0 15px;
}
.bet .settings-title {
  font-weight: bold;
  font-size: 130%;
  color: #019fe9;
  margin: 0;
}
.bet .settings-top-margin-big {
  margin-top: 29px;
}
.bet .settings-top-margin-normal {
  margin-top: 20px;
}
.bet .settings-top-margin-small {
  margin-top: 10px;
}
.bet .settings-button {
  display: block;
  line-height: 30px;
  color: #777;
  background: #FFF;
  border: 1px solid #777;
  border-radius: 10px;
  text-align: center;
  font-weight: bold;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
.bet .settings-button:hover {
  border: 1px solid #019fe9;
  color: #fff;
  background-color: #019fe9;
  text-align: center;
}
.bet .settings-button-selected {
  border: 1px solid #019fe9;
  color: #fff;
  background-color: #019fe9;
  text-align: center;
}
.bet .settings-text,
.bet .settings-row-label {
  font-size: 110%;
  color: #777;
}
.bet .settings-text-darker {
  font-size: 110%;
  color: #666 !important;
}
.bet .settings-switch {
  position: absolute;
  margin-left: -9999px;
  visibility: hidden;
}
.bet .settings-switch + label {
  display: block;
  position: relative;
  cursor: pointer;
  outline: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.bet input.settings-switch + label {
  padding: 2px;
  width: 40px;
  /*width: 120px;*/
  height: 20px;
  /*height: 60px;*/
  background-color: #dddddd;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  border-radius: 10px;
}
.bet input.settings-switch + label:before,
.bet input.settings-switch + label:after {
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
  bottom: 1px;
  content: "";
}
.bet input.settings-switch + label:before {
  right: 1px;
  background-color: #f1f1f1;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  border-radius: 10px;
  -webkit-transition: background 0.4s;
  -moz-transition: background 0.4s;
  -o-transition: background 0.4s;
  transition: background 0.4s;
}
.bet input.settings-switch + label:after {
  width: 20px;
  background-color: #fff;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  border-radius: 50%;
  -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  -webkit-transition: margin 0.4s;
  -moz-transition: margin 0.4s;
  -o-transition: margin 0.4s;
  transition: margin 0.4s;
}
.bet input.settings-switch:checked + label:before {
  background-color: #019fe9;
}
.bet input.settings-switch:checked + label:after {
  margin-left: 20px;
}
.bet .settings-row {
  padding: 10px 0;
}
.bet .settings-row .settings-submit-button {
  border: 0;
}
.bet .settings-row .settings-button,
.bet .settings-row .settings-submit-button {
  display: inline-block;
}
.bet .settings-row-switch-container {
  padding-top: 10px;
  float: right;
}
.bet ::-webkit-input-placeholder {
  color: #DDD;
}
.bet :-moz-placeholder {
  color: #DDD;
}
.bet ::-moz-placeholder {
  color: #DDD;
}
.bet :-ms-input-placeholder {
  color: #DDD;
}
.bet .settings-textbox {
  border-radius: 20px;
  padding: 3px;
  border: 1px solid #bbb;
  line-height: 18px;
  width: 200px;
}
.bet .settings-submit-button {
  display: block;
  border: 0;
  color: #fff;
  background-color: #019fe9;
  text-align: center;
  line-height: 30px;
  border-radius: 10px;
  min-width: 120px;
  opacity: 1;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.bet .settings-submit-button:hover {
  opacity: 0.5;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.bet .settings-date-interval {
  display: block;
  line-height: 30px;
  color: #777;
  border: 1px solid #777;
  border-radius: 10px;
  text-align: center;
  font-weight: bold;
  padding: 0 20px;
}
.bet .settings-date-interval > input[type='text'] {
  max-width: 47px;
  line-height: 24px;
  border: 0;
  border-radius: 10px;
  cursor: pointer;
}
.bet .settings-button-mini {
  line-height: 20px;
}
.bet .settings-hidden {
  display: none;
}